@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
body{font-family:"Muli",sans-serif;background-color:#f4f4f4}.App{text-align:center}.App-header{background-color:#f4f4f4;min-height:5vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(1rem + 2vmin);color:#0e0e0e}.print{display:none}.select-container{width:60vw}section input{padding:.5rem;display:flex;flex-direction:row;justify-content:space-between;margin:1rem}.word{display:block}.underline{display:inline-flex;padding:1rem;border-bottom:#222 solid 1px;margin:.2rem}.letter{display:inline-flex;padding:1rem;border:#222 solid 1px}.letter.hanging{top:2rem;height:2rem;position:relative}.letter.tall{height:2rem}.sentence{font-size:1.2rem}.output{display:flex;justify-content:space-evenly}.input{display:flex;flex-direction:row;align-items:center;justify-content:space-evenly;margin:auto;max-width:50vw}@media print{.no-print,.no-print *{display:none !important}.print,.print *{display:block;font-size:.8rem}}@media only screen and (max-width: 600px){.input{flex-direction:column;align-items:center;justify-content:space-around;margin:auto}}
